.page-header {
  margin: 0 auto;
  width: 90%;
  max-width: 1600px;
  padding-top: 2rem; }

.logo-container {
  margin: 0;
  text-align: center; }

.logo-link {
  display: block; }

.page-master-menu {
  list-style: none;
  margin: 0;
  text-align: center; }

.page-master-menu-item {
  display: inline-block;
  margin: 0 30px 0 0; }

.page-master-menu-link {
  font-size: 1.125rem;
  letter-spacing: 2px;
  text-transform: uppercase; }

@media only screen and (min-width:40.063em) {
  .page-header {
    font-size: 0; }
  .logo-container {
    display: inline-block;
    text-align: left;
    width: 50%; }
  .page-master-menu {
    display: inline-block;
    text-align: right;
    width: 50%; } }

.page-body {
  background-color: #FFFFFF;
  margin: 2rem auto 1rem auto;
  width: 90%;
  padding: 15px;
  max-width: 640px; }

@media only screen and (min-width:64.063em) {
  .page-body {
    max-width: 1600px; } }

.page-footer {
  margin: 0 auto;
  width: 90%;
  max-width: 1600px;
  padding: 1rem;
  color: #888888;
  background-color: #F8F8F8; }
  .page-footer .page-footer-content {
    letter-spacing: 1px;
    text-transform: uppercase; }

